    // Get ATK values for each weapon (for dual wield separate rolls)
    function getWeaponATKs(actor) {
        let weapons = (actor.cardStack || []).filter(card =>
            card.type === "item" &&
            card.subtype === "weapon" &&
            card.atk
        );
        return weapons.map(w => ({ name: w.name, atk: w.atk || 0 }));
    }

