    function setVoice(v) { gameVoice = v; }

    //  Expose on CardGame.AI namespace 
    Object.assign(window.CardGame.AI, {
        CardGameVoice,
        getPlayerEmotion,
        getPlayerMoodColor,
        buildEmotionContext,
        getVoice,
        setVoice
    });

