      pageButtons.push(paginationButton("button", "chevron_left", "", function () { navPrev(); }));

      let bMore = (pages.pageCount > 7)
      let left = pages.currentPage - 3;

      if (left < 1) left = 1;

      let right = pages.currentPage + 3;
