    function createParticle(p) {
        return {
            x: p ? p.x : Math.random() * canvas.width,
            y: p ? p.y : Math.random() * canvas.height,
            vx: (Math.random() - 0.5) * 0.5,
            vy: (Math.random() - 0.5) * 0.5,
            radius: Math.random() * 3 + 1,
            alpha: Math.random() * 0.5 + 0.1
        };
    }

